@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";
@import url("https://fonts.googleapis.com/css2?family=Amaranth&family=Bricolage+Grotesque:opsz@12..96&family=Domine&family=IBM+Plex+Mono&family=Libre+Baskerville&family=Montserrat:wght@400;500;700;900&family=Playpen+Sans&family=Poppins:wght@300&family=Unna&display=swap");
:root {
  --primary-color: #f7444e;
  --secondary-color: #002c3e;
  --secondary-font: "Playfair Display", serif;
  --item-color: #f7f8f9;
}
@layer base {
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Montserrat", sans-serif;
  }
  body {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #0c0c0c;
  }

  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }
  .nav-link.active {
    color: #f7444e;
  }
  .main-banner {
    background-image: url(./Assets/slider-bg.jpg);
    background-size: cover;
  }
  .main-heading::after {
    position: absolute;
    bottom: -22px;
    left: 47%;
    display: inline-block;
    content: "";
    width: 5rem;
    height: 5px;
    background-color: #f7444e;
  }
  .new-arrival-wrapper {
    background-color: #d4ced0;
  }
  .new-arrival-wrapper img {
    width: 50%;
    -o-object-fit: cover;
    object-fit: cover;
    -o-object-position: top left;
    object-position: top left;
  }
  .item-wrapper:hover .item-popup {
    visibility: visible;
    height: 100%;
    cursor: pointer;
    z-index: 15;
  }
  .item-popup,
  .item-img {
    transition: all 0.3s cubic-bezier(0.4, 0, 1, 1);
  }
  .item-wrapper:hover .item-img {
    transform: scale(1.2);
  }
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  .selection::after { 
    content: "OR";
    font-weight: bold;
    position: absolute;
    bottom: -30px;
    left: 40%;
    color: black;
    background: white;
  }
}
